.tab-btn {
    padding: 7px 10px;
    border: 1px solid #61dafb;
    border-radius: 3px;
    font-size: 1rem;
    color: #61dafb;
    background-color: transparent;
    cursor: pointer;
    transition: all 0.1s linear;
}

.tab-btn:hover {
    color: white;
    background-color: #61dafb;
}

.active {
    color: white;
    background-color: #61dafb;
}
